body {
  margin: 0;
  padding: 0;
  font-family: 'Averta Std CY';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.header{
    position: fixed;
    z-index: 1000;
    width: 100%;
    box-sizing: border-box;
    top: 0;
    /* background-color: rgb(76, 167, 87, 0.9); */
}
.logo{
    width: 122px;
    height: 74px;
    margin-top: 19px;
    margin-left: 7ch;
}
.header > div{
    float: right;
    width: 152px;
    height: 40px;
    margin-top: 33px;
}
p{
    margin: 0;
}

.navbar-nav li:hover > ul.dropdown-menu {
    display: block;
}
.dropdown-submenu {
    position: relative;
    width: 320px;
    margin-left: 75px;
    border-bottom: 1px solid #fff;
}
.dropdown-submenu>a{
    font-size: 28px;
    font-weight: 600;
    line-height: 33.6px;
}
.dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top:-6px;
    padding-left: 240px;
    background-color: unset;
    border: none;
}

/* rotate caret on hover */
.dropdown-menu > li > a:hover:after {
    text-decoration: underline;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
} 

.navbar{
    margin: 20px 70px;
    padding: 0;
}

.nav-item{
    width: 200px;
    border-top: 1px solid #fff;
    margin: 0 12px;
}

.dropdown-item {
    color: #fff;
    padding-left: 0;
    padding-top: 30px;
    padding-bottom: 20px;
}

.dropdown-item:focus, .dropdown-item:hover{
    background-color: unset;
    color: #fff;
}


.navbar-light .navbar-nav .active>.nav-link,
.navbar-light .navbar-nav .show>.nav-link,
.navbar-light .navbar-nav .nav-link{
    color: #fff;
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover{
    color: #fff;
    border-top: 2px solid #fff;
}


.introduce-menu{
    width: 100vw;
    height: 85vh;
    background-color: rgba(22, 165, 113, 0.95);
    left: -306px;
    padding-top: 60px;
}

.content{
    width: 342px;
    margin-left: 32px;
    display: grid;
    align-content: center;
}
.content>p{
    font-size: 39px;
    font-weight: 700;
    line-height: 30px;
}
.content>span{
    white-space: break-spaces;
}
.content>a{
    color: #fff;
    text-decoration: none;
}

.newsEvent-menu{
    width: 100vw;
    height: 85vh;
    background-color: rgba(22, 165, 113, 0.95);
    left: -978px;
    padding-top: 60px;
}

.carousel-item {
    position: relative;
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
    transition: opacity 1s, -webkit-transform 1s;
    transition: opacity 1s, transform 1s;
    transition: opacity 1s, transform 1s, -webkit-transform 1s;
}
  
.carousel-item.active {
opacity: 1;
-webkit-transform: translateY(0);
        transform: translateY(0);
}

.carousel-caption{
    position: absolute;
    width: 1763px;
    bottom: 195px;
    height: 440px;
    left: 0;
    z-index: 10;
    padding-top: 0;
    padding-bottom: 0;
    color: #fff;
    margin: 0 70px;
}

.banner-leftContent{
    float: left;
    width: 800px;
    text-align: left;
}

.banner-rightContent{
    position: absolute;
    width: 448px;
    text-align: left;
    bottom: 0;    
    right: 70px;
}
.banner-rightContent > p{
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
}



.carousel-indicators{
    bottom: -80px;
    align-items: center;
}

.carousel-indicators img{
    cursor: pointer;
    margin: 0 200px;
}

.carousel-indicators .active {
    background-color: unset;
}


.textRun{
    font-family: Averta Std CY;
    font-size: 100px;
    font-weight: 800;
    line-height: 150px;
    letter-spacing: 0em;
    text-align: left;
    text-transform: uppercase;
    margin: 24px 0;
}
.aMemberOf {
  display: grid;
  width: 100%;
  height: 950px;
  padding: 100px 70px;
  background-image: url(/test/static/media/bgr.07988771.png);
}

.slideshow-container-test {
  width: 490px;
  position: relative;
}
.slide-test {
  display: none;
}

.slide-test img {
  width: 100%;
  height: 200px;
}

button:focus {
  outline: none;
}

.prev-test,
.next-test {
  cursor: pointer;
  position: absolute;
  top: 150%;
  color: white;
  font-weight: bold;
  font-size: 18px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  background-color: #52b279;
}
.prev-test:hover {
  background-color: #d9eee1;
}
.next-test:hover {
  background-color: #d9eee1;
}

.next-test {
  left: 70px;
}

.active-test {
  /* background-color: #717171; */
}
.slide-btn-test {
  padding: 10px 20px;
}

.btn-img {
  position: absolute;
  right: 3%;
  width: 800px;
  height: 300px;
  display: flex;
  align-items: center;
}

.slide-text{
    font-size: 20px;
    line-height: 33px;
    font-weight: 400;
    color: #fff;    
}

.amember-content{
    display: grid;
    align-items: end;
}

.amember-content>p{
    font-size: 46px;
    font-weight: 800;
    line-height: 56px;
    letter-spacing: -0.05em;
    text-align: left;
    color: #fff;
}

.count-auto{
    display: flex;
    align-items: end;
}
.empowring{
    width: 100%;
    height: auto;
    padding: 100px 70px;
}

.empowring p:first-child{
    font-size: 18px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0.12em;
    color: #376A66;
}

.empowring p:nth-of-type(2){
    font-size: 60px;
    font-weight: 800;
    line-height: 64px;
    letter-spacing: -0.05em;
    text-align: left;
    color: #4CA757;
}

.empowring img{
    padding-right: 6px;
    padding-bottom: 8px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

ul{
    list-style-type: none;
    display: flex;
    padding: 0;
    margin-bottom: 0;
}

ul li:hover img {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    z-index: 100;
}


.news{
    width: 100%;
    padding: 100px 70px;
    display: grid;
}


.slideshow-container-new {
  display: flex; 
  overflow-x: hidden; 
}

.slide-new {
  flex: 0 0 100%; 
  max-width: 25%; 
  transition: -webkit-transform 0.5s ease-in-out; 
  transition: transform 0.5s ease-in-out; 
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}



.prev-new,
.next-new {
    color: #52b279;
    font-weight: bold;
    font-size: 18px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: none;
    background-color: #fff;
    margin: 25px 10px;
    border: 1px solid #52b279;
}
.prev-new:hover {
  background-color: #52b279;
  color: #fff;
}
.next-new:hover {
  background-color: #52b279;
  color: #fff;
}

.next-new {
  left: 130px;
}

.slide-text-new{
    width: 100%;
    height: 180px;
    font-size: 20px;
    line-height: 33px;
    font-weight: 600;
    color: black;    
    padding: 14px;
}

.slide-text-new>p{
    color: #376A66;
    font-size: 16px;
    line-height: 27px;
    font-weight: 400;
}

.swiper-pagination-new {
    width: 80%;
    height: 6px;
    background-color: #ccc;
    border-radius: 5px;
  }
  
  .swiper-pagination-progressbar-fill-new {
    display: block;
    height: 100%;
    background-color: #52b279;
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: -webkit-transform 300ms;
    transition: transform 300ms;
    transition: transform 300ms, -webkit-transform 300ms;
  }

.new-content{
    width: 90%;
}

.new-content:hover{
    background-color: #4CA757;
}

.new-content:hover .slide-text-new{
    color: #fff;
}

.new-content:hover .link-news{
    text-decoration: none;
    color: #fff;
}

.link-news{
    float: right;
    text-decoration: none;
    color: black;
    font-size: 16px;
}


